.filterContainer {
  padding: 15px;

  hr {
    margin-top: 0px;
    margin-bottom: 1rem;
  }
}

.section {
  margin-bottom: 2rem;
}

.sectionContent {
  & > div {
    margin-right: 1px;
  }
}

.container {
  min-width: 700px;
}

.error {
  color: #e40000;
  font-family: Verdana;
  font-size: 12px;
  line-height: 15px;
  margin-top: 8px;
  margin-bottom: 0px;
}

.search {
  padding-left: 10px;
  display: flex;
  align-items: center;
  border: 2px solid #ccc;
  border-radius: 25px;
  flex: 1;
}

.dropDownHeader {
  border: none !important;

  &:focus {
    border: none !important;
    box-shadow: none !important;
    outline: 0;
  }

  &:active {
    border: none !important;
    box-shadow: none;
  }
}

.dropDown {
  border: none;
}

.textField {
  flex: 1;
}

.currencyField {
  span {
    font-size: 16px;
  }
  input[type='number'] {
    text-align: right;
    font-size: 16px;
    appearance: none;
    margin: 0;

    &:focus {
      outline: none !important;
      box-shadow: none !important;
      border: 1px solid #ced4da;
    }
  }
}

.dropDownMenu {
  min-width: 850px;
}

.searchIcon {
  color: '#cdcdcd';
}

.textField {
  border: none !important;

  &:active {
    outline: none !important;
  }

  &:focus {
    outline: none !important;
  }
}

