.tabs {
  border-bottom: none;
}

.navLink {
  text-transform: uppercase;
  border: none !important;
  font-size: 14px;
  font-weight: medium;

  &:focus,
  &:hover {
    border: none;
    cursor: pointer;
  }
}

.padding {
  padding: 15px !important;
  background-color: #ccc;
  color: #919496 !important;
}

.active {
  font-size: 14px;
  color: #0069d9 !important;
  border: none !important;
  border-bottom: 2px solid !important;
  border-color: #0069d9 !important;
  text-transform: uppercase;
  font-weight: bold;

  &:focus,
  &:hover {
    cursor: pointer;
  }
}
