.table {
  th {
    text-align: center;
    font-size: 12px;
  }

  td {
    text-align: center;
    font-size: 12px;
    vertical-align: middle;
    justify-content: center;
    max-width: 350px !important;

    p {
      margin-bottom: 0;
    }
  }
}

.pointer {
  cursor: pointer;
}

.hasChange {
  color: red;
}

.hasNotChange {
  color: #000;
}

.correction {
  border: 1px solid #ccc;
  border-left: 20px solid #f5f5f5;
  border-right: 20px solid #f5f5f5;
}
